body {
  font-family: "Raleway-Regular";
  text-rendering: geometricPrecision;
  color: #333333;
  line-height: 1.6em;
}

html {
  scroll-behavior: smooth;
}

.centered, .center {
  text-align: center;
}

a, button {
  color: inherit;
  text-decoration: inherit;
  border: 0;
}

button, .button {
  cursor: pointer;
  max-width: 300px;
  width: 100%;
  padding: 15px 25px;
  border-radius: 100px;
  font-size: 20px;
  display: block;
}
button.green, .button.green {
  background: #D1FD38;
  color: #0088CC;
}
button.blue, .button.blue {
  background: #0088CC;
  color: #D1FD38;
}
button.centered, .button.centered {
  margin: 0 auto;
}
@media (max-width: 600px) {
  button, .button {
    font-size: 16px;
  }
}

#whatsapp {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

svg {
  max-width: 100%;
}

.intro {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 850px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.intro > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center;
  position: absolute;
}
.intro .main-title {
  position: relative;
  display: block;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color: #fff;
  padding: 0 35px;
  min-height: 500px;
  max-height: 400px;
  font-size: 20px;
}
@media (max-width: 724px) {
  .intro .main-title {
    padding: 0 15px;
  }
}
.intro .main-title img {
  width: 100%;
  max-width: 600px;
}

.descanso {
  padding: 100px 15px;
}
@media (max-width: 600px) {
  .descanso {
    padding: 60px 15px;
  }
}
.descanso div {
  padding: 80px 0;
  max-width: 1024px;
  margin: 0 auto;
  font-size: 25px;
  border-top: 1px dotted rgba(187, 189, 191, 0.5);
  border-bottom: 1px dotted rgba(187, 189, 191, 0.5);
  line-height: 1.5em;
}
@media (max-width: 600px) {
  .descanso div {
    padding: 35px 0;
    font-size: 20px;
  }
}
.descanso div p {
  padding: 0 35px;
}
@media (max-width: 600px) {
  .descanso div p {
    padding: 0;
  }
}
.descanso.no-border div {
  border: 0;
}
.descanso.no-border div p {
  padding: 0;
  line-height: 1.8em;
  font-size: 22px;
}

.fullWidthImage {
  width: 100%;
  max-height: 800px;
  height: 80vh;
}

#img-1 {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}

.descanso-2 {
  background: #D1FD38;
  color: #0088CC;
  font-size: 22px;
  padding: 80px 35px;
}
.descanso-2 div {
  max-width: 1024px;
  margin: 0 auto;
  line-height: 1.5em;
}
.descanso-2 div img {
  margin-left: -15px;
}
.descanso-2 div p {
  padding-top: 30px;
