.siete-pilares {
  background: #0088CC;
}
.siete-pilares .info {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 15px;
}
.siete-pilares .info .titulo {
  display: flex;
  align-items: flex-end;
}
.siete-pilares .info .titulo #excelencia {
  margin-left: 35px;
}
@media (max-width: 600px) {
  .siete-pilares .info .titulo {
    display: block;
  }
  .siete-pilares .info .titulo #excelencia {
    margin-left: 0;
    max-width: 100%;
  }
}
.siete-pilares .info .pilares {
  padding: 100px 0;
  color: #fff;
  font-size: 20px;
  text-align: center;
  width: 100%;
}
@media (max-width: 600px) {
  .siete-pilares .info .pilares {
    padding: 50px 0 80px;
    font-size: 16px;
  }
}
@media (min-width: 1024px) {
  .siete-pilares .info .pilares {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 4em 2rem;
  }
}
@media (max-width: 1023px) {
  .siete-pilares .info .pilares {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4em 2rem;
  }
}
@media (max-width: 600px) {
  .siete-pilares .info .pilares {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 4rem;
  }
}
.siete-pilares .info .pilares .pilar .icon {
  width: 135px;
  height: 135px;
  background: #fff;
  border-radius: 150px;
  margin: 0 auto 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.siete-pilares .info .pilares .pilar .icon .image svg path {
  fill: #0088CC;
}
.siete-pilares #pilar-5 svg path {
  fill: #fff;
}
.siete-pilares #pilar-5 svg path:nth-child(1) {
  fill: #0088CC;
}

.soluciones {
  width: 100%;
  max-height: 700px;
  height: 100vh;
  background-color: #0088CC;
  display: flex;
  align-items: center;
  background: url("../images/3.A.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
}
.soluciones::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #0088CC;
  opacity: 0.5;
}
.soluciones .info {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 auto;
  flex-direction: column;
  height: 100%;
  min-height: 400px;
  max-height: 60%;
  position: relative;
}
.soluciones .title {
  font-size: 59px;
  line-height: 1em;
  color: #fff;
}
.soluciones .icon svg path {
  fill: #D1FD38;
}
@media (max-width: 600px) {
  .soluciones .title {
    font-size: 2.5rem;
  }
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.residuos {
  background: #F4F4F4;
  padding: 80px 35px;
}
.residuos .title {
  color: #0088CC;
  padding-bottom: 7rem;
  font-size: 3rem;
  line-height: 2rem;
}
@media (max-width: 1023px) {
  .residuos .title {
    font-size: 2rem;
    padding-bottom: 4rem;
  }
}
.residuos .residuos-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  grid-gap: 6em 4rem;
}
@media (max-width: 1023px) {
  .residuos .residuos-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4em 2rem;
  }
}
@media (max-width: 600px) {
  .residuos .residuos-list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 4rem;
  }
}
.residuos .residuos-list .icon img {
  max-width: 100%;
}
.residuos .residuos-list .name {
  padding-top: 15px;
  font-size: 1.8rem;
}

.contacto {
  background: #D1FD38;
  padding: 100px 15px;
}
.contacto .datos {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  grid-gap: 6em 4rem;
  font-size: 16px;
}
@media (max-width: 1023px) {
  .contacto .datos {
    grid-template-columns: 1fr;
    grid-gap: 4rem;
  }
}
.contacto .datos .title {
  font-size: 20px;
  margin-bottom: 2rem;
}
.contacto .datos .icon {
  margin-bottom: 2rem;
}
.contacto .form form {
  max-width: 760px;
  margin: 80px auto 0;
}
.contacto .form form .input {
  margin-bottom: 1rem;
  display: block;
}
.contacto .form form .input input, .contacto .form form .input textarea {
  display: block;
  width: 100%;
  padding: 7px 15px;
  color: #333333;
  box-sizing: border-box;
}
.contacto .form form .input textarea {
  min-height: 150px;
  margin-bottom: 3rem;
  resize: none;
}

.carousel {
  width: 100%;
  max-width: 100%;
  height: 80vh;
  max-height: 100%;
  overflow: hidden;
  position: relative;
}
.carousel .nav {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 10px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: inline-block;
  z-index: 2;
}
.carousel .nav li {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: white;
  opacity: 0.5;
  border-radius: 25px;
  margin: 0 5px;
  cursor: pointer;
}
.carousel .nav li.active {
  opacity: 1;
}
.carousel .slides-container {
  width: 100%;
  max-width: 100%;
  height: 100vh;
  max-height: 700px;
  min-height: 60vh;
  overflow: hidden;
}
.carousel .slides-container .slide {
  width: 100%;
  height: 100%;
  transition: all 0.5s ease-in;
  opacity: 0;
  position: absolute;
  z-index: 0;
}
.carousel .slides-container .slide.active {
  opacity: 1;
}
.carousel .slides-container .slide img {
  width: 100%;
  height: 100%;
}